/**
* @description 
* @param {String} input 
* @returns {String} 
*/
let trim = (function (isNative) {
    return function (input) {
        return isNative ? isNative.apply(input) : ((input || '') + '').replace(/^\s+|\s+$/g, '');
    }
})(String.prototype.trim);

/**
 * @description  
 * @param {any} str
 * @returns {Array} 
 */
let splitWords = function (str) {
    return trim(str).split(/\s+/);
}

export { splitWords, trim };