/**
 *  ext VertexArrayObject
 * reference https://developer.mozilla.org/zh-CN/docs/Web/API/OES_vertex_array_object
 * 
 * @class GLVertexArrayObject
 */
import Dispose from './../utils/Dispose';
import GLExtension from './GLExtension';
import GLConstants from './GLConstants';
/**
 * @class
 */
class GLVertexArrayObject extends Dispose {
    /**
     *  indexbuffer  vertexbuffer
     */
    _indexBuffer = null;
    /**
     *  vertexbuffer
     */
    _attributes = [];
    /**
     * @type {GLExtension}
     */
    _ext;
    /**
     * @type {WebGLRenderingContext}
     */
    _gl;
    /**
     * attributeactiveactivevaoactive
     * @type {boolean}
     */
    _needsToActive = false;
    /**
     * @param {WebGLRenderingContext} gl 
     * @param {GLExtension} extension 
     * @param {GLLimits} limits
     */
    constructor(gl, extension, limits) {
        super();
        this._gl = gl;
        this._ext = extension['vertexArrayObject'];
        this._handle = this._createHandle();
    }
    /**
     * vao
     * @description polyfill
     * @return va
     */
    _createHandle() {
        const gl = this._gl,
            ext = this._ext;
        if (!!ext) 
            return ext.createVertexArrayOES();
        if (!!gl.createVertexArray)
            return gl.createVertexArray();
        return null;
    }
    /**
     * vao
     */
    dispose() {
        
    }
    /**
     * 
     */
    bind() {
        const ext = this._ext,
            gl = this._gl;
        if (!!ext)
            ext.bindVertexArrayOES(this._handle);
        else
            gl.bindVertexArray(this._handle);
    }
    /**
     * 
     */
    unbind() {
        const ext = this._ext,
            gl = this._gl;
        if (!!ext)
            ext.bindVertexArrayOES(null);
        else
            gl.bindVertexArray(null);
    }
    /**
     * vertexbufferindexbuffer
     */
    _active() {
        this._activeVertexBuffer();
        this._activeIndexBuffer();
    }
    /**
     * indexbuffer
     */
    _activeIndexBuffer() {
        this._indexBuffer.bind();
    }
    /**
     * bufferDatavao
     */
    _activeVertexBuffer() {
        const gl = this._gl;
        // vertexbuffer 
        for (let i = 0, len = this._attributes.length; i < len; i++) {
            const att = this._attributes[i];
            //1.bind buffer
            att.buffer.bind();
            //2.setting vertexAttrib
            gl.vertexAttribPointer(att.location, att.size, att.type, att.normalized, att.stride, att.offset);
        }
    }
    /**
     * 
     * @param {GLVertexBuffer} buffer ,buffer
     * @param {number} location , gPositionLocation
     * @param {number} size,  [x,y] size=2 ,[x,y,z,w] size=4
     * @param {boolean} normalized 
     * @param {number} stride 00
     * @param {number} offset GL_ARRAY_BUFFER0
     */
    addAttribute(buffer, location, size, normalized, stride, offset) {
        this._attributes.push({
            glBuffer: buffer,
            location: location,
            size: size,
            type: buffer.type || GLConstants.FLOAT,
            normalized: normalized || false,
            stride: stride || 0,
            offset: offset || 0
        });
    }
    /**
     * reference
     * http://www.cnblogs.com/excalibur/articles/1573892.html
     * indexbuffer
     * - vertexbuffer
     * - 
     * vertex cacheindexbuffer
     * @param {GLIndexBuffer} buffer 
     */
    addIndex(buffer) {
        this._indexBuffer = buffer;
    }
    /**
     * vao
     */
    clear() {
        this.unbind();
        this.bind();
        this._attributes = [];
        this._indexBuffer = null;
    }
    /**
     * 
     * @param {*} type 
     * @param {*} size 
     * @param {number} offset 
     */
    draw(type, size, offset) {
        const gl = this._gl;
        if (!!this._indexBuffer) {
            gl.drawElements(type, size || this._indexBuffer.length, this._indexBuffer.type, (offset || 0) << 1)
        } else {
            gl.drawArrays(type, offset, size || this.size);
        }
    }
    /**
     * vao
     */
    get size() {
        var attrib = this._attributes[0];
        return attrib.buffer.length / ((attrib.stride / 4) || attrib.size);
    }
}

export default GLVertexArrayObject;